import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/views/Home.vue'
import Test from '@/views/Test.vue'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/test',
      name: 'Test',
      component: Test
    }
  ]
})

export default router
