<template>
  <div class="x-home">
    <el-upload class="upload-demo" multiple action="" :on-change="uploadAction" :auto-upload="false"
      :file-list="fileList">
      <el-button size="small" type="primary">ÁÇπÂáª‰∏ä‰º†</el-button>
    </el-upload>
    <el-divider content-position="left">Êìç‰ΩúË°®Ê†º</el-divider>
    <el-button size="small" type="primary" @click="previewTables('preview')">È¢ÑËßàË°®Ê†º</el-button>
    <el-input-number v-model="num" label="ÊåâÁ¨¨Âá†ÂàóÂàÜÁªÑ" size="small" style="margin: 0 20px"></el-input-number>
    <el-button size="small" type="primary" @click="exportTable('one')">ÂØºÂá∫‰∏Ä‰∏™Ë°®Ê†º</el-button>
    <el-button size="small" type="primary" @click="exportTable('more')">ÂØºÂá∫Â§ö‰∏™Ë°®Ê†º</el-button>
    <el-divider content-position="center">üò∫</el-divider>
    <div v-if="index === 'preview'" style="width: 100%; height: 400px;overflow: auto">
      <div style="width: 100%;margin-right: 8px;float:left" :key="'table-' + i" v-for="(table, i) in tables">
        <p style="font-size: 12px">{{ table.title }}</p>
        <el-table :data="table.list" border stripe size="small">
          <el-table-column v-for="(column, index) in table.cols" :key="'x-' + index" :fixed="index === 0"
            :prop="column.id" :label="column.name"></el-table-column>
        </el-table>
      </div>
    </div>
    <div v-else-if="index === 'new'" style="width: 100%; height: 400px;overflow: auto">
      <div style="width: 100%;margin-right: 8px;float:left">
        <p style="font-size: 12px">{{ result.title }}</p>
        <el-table :data="result.list" border stripe size="small">
          <el-table-column v-for="(column, index) in result.cols" :key="'x-' + index" :prop="column.id"
            :label="column.name"></el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>
<script>
import XLSX from 'xlsx'
export default {
  name: 'Home',
  props: {
    item: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      tables: [],
      result: { title: 'ÂêàÂπ∂ÂêéÁöÑË°®Ê†º', list: [], cols: [] },
      fileList: [],
      index: 'xxx',
      num: 4
    }
  },
  methods: {
    previewTables: function (index) {
      this.index = index
      this.tables = []
      for (let index = 0; index < this.fileList.length; index++) {
        const file = this.fileList[index]
        const table = { title: file.name, list: [], cols: [] }
        this.tables.push(table)
        this.processFile(file, table)
      }
    },
    createTable: function (index) {
      this.index = index
      if (this.fileList.length < 3) {
        alert('Â∫îËØ•ÊòØ‰∏â‰∏™Ë°®Ê†º')
      }
      for (let i = 0; i < this.tables[2].list.length; i++) {
        const item1 = this.tables[2].list[i]
        let item2 = null
        let item3 = null
        for (let j = 0; j < this.tables[1].list.length; j++) {
          const element = this.tables[1].list[j]
          const keys = Object.keys(element)
          for (let index = 0; index < keys.length; index++) {
            const key = keys[index]
            if (element[key] + '' === item1.column0 + '') {
              item2 = element
            }
          }
        }
        for (let j = 0; j < this.tables[0].list.length; j++) {
          const element = this.tables[0].list[j]
          const keys = Object.keys(element)
          for (let index = 0; index < keys.length; index++) {
            const key = keys[index]
            if (element[key] + '' === item1.column2 + '') {
              item3 = element
            }
          }
        }
        if (item1 && item2 && item3) {
          console.log(item1, item2, item3)
        } else {
          console.log('‰∏çÂêàÊ≥ï')
        }
      }
    },
    exportTable: function (type = 'one') {
      const list = this.tables[0].list
      const headerReplace = {}
      for (let index = 0; index < this.tables[0].cols.length; index++) {
        const cols = this.tables[0].cols[index]
        headerReplace[cols.id] = cols.name
      }
      const groups = []
      const key = 'column' + (this.num - 1)
      for (let index = 0; index < list.length; index++) {
        const item = list[index]
        const target = groups.find(t => t.name === item[key])
        if (!target) {
          const group = { name: item[key], list: list.filter(col => col[key] === item[key]) }
          group.list.unshift(headerReplace)
          groups.push(group)
        } else {
          console.log('Â∑≤Â≠òÂú®', item[key])
        }
      }

      if (type === 'one') {
        // Êñ∞Âª∫book
        const wb = XLSX.utils.book_new()
        for (let index = 0; index < groups.length; index++) {
          const group = groups[index]
          if (group.list.length) {
            console.log('ÁªÑ:', index, group.name)
            const item = group.list[0]
            const keys = Object.keys(item)
            // Êñ∞Âª∫Á©∫workbookÔºåÁÑ∂ÂêéÂä†ÂÖ•worksheet
            const ws = XLSX.utils.json_to_sheet(group.list, { skipHeader: true })
            // ËÆæÁΩÆÊØèÂàóÁöÑÂàóÂÆΩÔºå10‰ª£Ë°®10‰∏™Â≠óÁ¨¶ÔºåÊ≥®ÊÑè‰∏≠ÊñáÂç†2‰∏™Â≠óÁ¨¶
            ws['!cols'] = []
            for (let index = 0; index < keys.length; index++) {
              const key = keys[index]
              if (item[key]) {
                ws['!cols'].push({ wch: (item[key] + '').length * 2 + 10 })
              } else {
                ws['!cols'].push({ wch: 10 })
              }
            }
            // ÁîüÊàêxlsxÊñá‰ª∂(book,sheetÊï∞ÊçÆ,sheetÂëΩÂêç)
            XLSX.utils.book_append_sheet(wb, ws, group.name)
          }
        }
        // ÂÜôÊñá‰ª∂(book,xlsxÊñá‰ª∂ÂêçÁß∞)
        XLSX.writeFile(wb, 'ÂàÜÁªÑÂêé.xlsx')
      } else {
        // Êñ∞Âª∫book
        for (let index = 0; index < groups.length; index++) {
          const group = groups[index]
          if (group.list.length) {
            console.log('ÁªÑ:', index, group.name)
            const wb = XLSX.utils.book_new()
            const item = group.list[0]
            const keys = Object.keys(item)
            // Êñ∞Âª∫Á©∫workbookÔºåÁÑ∂ÂêéÂä†ÂÖ•worksheet
            const ws = XLSX.utils.json_to_sheet(group.list, { skipHeader: true })
            // ËÆæÁΩÆÊØèÂàóÁöÑÂàóÂÆΩÔºå10‰ª£Ë°®10‰∏™Â≠óÁ¨¶ÔºåÊ≥®ÊÑè‰∏≠ÊñáÂç†2‰∏™Â≠óÁ¨¶
            ws['!cols'] = []
            for (let index = 0; index < keys.length; index++) {
              const key = keys[index]
              if (item[key]) {
                ws['!cols'].push({ wch: (item[key] + '').length * 2 + 10 })
              } else {
                ws['!cols'].push({ wch: 10 })
              }
            }
            // ÁîüÊàêxlsxÊñá‰ª∂(book,sheetÊï∞ÊçÆ,sheetÂëΩÂêç)
            XLSX.utils.book_append_sheet(wb, ws, group.name)
            XLSX.writeFile(wb, group.name + '.xlsx')
          }
        }
        // ÂÜôÊñá‰ª∂(book,xlsxÊñá‰ª∂ÂêçÁß∞)
      }
    },
    getGroups: function (arr, key) {
      var map = {}
      arr.forEach(i => {
        // Ê†πÊçÆÊØè‰∏ÄÈ°πÁöÑidËøõË°åÂà§Êñ≠ÔºåÂ¶ÇÊûúidÁõ∏ÂêåÔºåÂàôÂ∫îÂú®‰∏Ä‰∏™Êñ∞Êï∞ÁªÑÈáå
        // Â¶ÇÊûú‰πãÂâçÊ≤°Êúâ‰ª•Ê≠§id‰∏∫ÂàÜÁªÑÁöÑÊï∞ÁªÑ
        if (!map[i[key]]) {
          const item = {
            list: [i] // listÈáåÂ≠òÊîæÂÆåÊï¥ÁöÑÊï∞ÊçÆ
          }
          item[key] = i[key]
          // ÈÇ£‰πàÂàõÂª∫Ëøô‰∏™ÁªÑ
          map[i[key]] = item
        } else {
          // Â¶ÇÊûúÂ∑≤ÁªèÊúâËøô‰∏™Êï∞ÁªÑ ÊèíÂÖ•Êï∞ÊçÆ
          map[i[key]].list.push(i)
        }
      })

      // ÂÜçÂÆö‰πâ‰∏Ä‰∏™Á©∫Êï∞ÁªÑËøõË°åËøáÊª§
      const list = []
      for (var i in map) {
        list.push(map[i])
      }
      return list
    },
    uploadAction: function (file, fileList) {
      console.log(file, fileList)
      this.fileList.push(file)
    },
    processFile (file, table) {
      const reader = new FileReader()
      reader.onload = e => {
        /* Ëß£ÊûêÊï∞ÊçÆ */
        const bstr = e.target.result
        const wb = XLSX.read(bstr, { type: 'binary' })
        /* Ëé∑ÂèñÊñá‰ª∂ÁöÑÁ¨¨‰∏Ä‰∏™Â∑•‰ΩúË°®ÔºàWorkSheetÔºâ */
        const wsname = wb.SheetNames[0]
        const ws = wb.Sheets[wsname]
        table.cols = this.getHeaderRow(ws)
        /* Êï∞ÁªÑËΩ¨Êç¢ */
        table.list = []
        const data = XLSX.utils.sheet_to_json(ws, { header: 1 })
        for (let index = 1; index < data.length; index++) {
          const columns = data[index]
          const item = {}
          for (let i = 0; i < columns.length; i++) {
            item['column' + i] = columns[i] ? columns[i] + '' : ''
          }
          table.list.push(item)
        }
        /* ËøõË°åË°®Ê†ºË°®Â§¥Êï∞ÊçÆÊõ¥Êñ∞ */
      }
      reader.readAsBinaryString(file.raw)
    },
    getHeaderRow: function (sheet) {
      const headers = []
      /* sheet['!ref']Ë°®Á§∫ÊâÄÊúâÂçïÂÖÉÊ†ºÁöÑËåÉÂõ¥Ôºå‰æãÂ¶Ç‰ªéA1Âà∞F8ÂàôËÆ∞ÂΩï‰∏∫ A1:F8 */
      const range = XLSX.utils.decode_range(sheet['!ref'])
      let C
      const R = range.s.r /* ‰ªéÁ¨¨‰∏ÄË°åÂºÄÂßã */
      /* ÊåâÂàóËøõË°åÊï∞ÊçÆÈÅçÂéÜ */
      for (C = range.s.c; C <= range.e.c; ++C) {
        /* Êü•ÊâæÁ¨¨‰∏ÄË°å‰∏≠ÁöÑÂçïÂÖÉÊ†º */
        const cell = sheet[XLSX.utils.encode_cell({ c: C, r: R })]

        let hdr = 'UNKNOWN ' + C // <-- ËøõË°åÈªòËÆ§ÂÄºËÆæÁΩÆ
        if (cell && cell.t) hdr = XLSX.utils.format_cell(cell)
        console.log(C)
        const item = { id: 'column' + C, name: hdr }
        headers.push(item)
      }
      return headers
    }
  }
}
</script>

<style scoped>
.x-home {
  padding: 20px;
}
</style>
